<template>
  <uv-button
    :customStyle="{ ...commonStyle, ...customStyle }"
    :disabled="disabled"
    :loading="loading"
    :loadingText="loadingText"
    @click="click"
    v-bind="$attrs"
    :throttleTime="300"
  >
    <slot></slot>
  </uv-button>
</template>

<script>
import themeMixins from '@/mixins/themeMixins.js'
export default {
  mixins: [themeMixins],
  options: {
    styleIsolation: 'shared', // 解除样式隔离
  },
  props: {
    loadingText: {
      type: String,
      default: '',
    },
    loading: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    customStyle: {
      type: Object,
      default: () => {
        return {}
      },
    },
    buttonType: {
      type: String,
      default: 'primary', // 主要按钮primary  次级按钮 lesser  镂空按钮 plain
    },
  },
  data() {
    return {}
  },
  computed: {
    commonStyle() {
      let baseObj = {
        height: '80rpx',
        'font-size': '28rpx',
        'line-height': '80rpx',
        'border-style': 'none',
        'border-radius': '200rpx',
      }
      // baseObj.background = `linear-gradient(227.21deg, ${this.themePrimaryColorGetter} 0%, ${this.themePrimaryLight2ColorGetter} 100%)`
      let styleObj = this.getButtonTypeStyle()
      if (this.disabled) {
        baseObj.opacity =
          this.themeConfigGetter[`--hubei-${this.buttonType}-button-disabled-opacity`]
      }
      return { ...baseObj, ...styleObj }
    },
  },
  methods: {
    click(e) {
      console.log('--------button')
      this.$emit('customClick', e)
      this.$emit('click', e)
    },

    getButtonTypeStyle() {
      let obj = {
        background: this.themeConfigGetter[`--hubei-${this.buttonType}-button-bg-color`],
        color: this.themeConfigGetter[`--hubei-${this.buttonType}-button-font-color`],
      }
      if (this.themeConfigGetter[`--hubei-${this.buttonType}-button-border-color`]) {
        obj.border = `1rpx solid ${this.themeConfigGetter[`--hubei-${this.buttonType}-button-border-color`]}`
      }
      return obj
    },
  },
}
</script>

<style lang="scss">
::v-deep .uv-button-wrapper {
  width: 100%;
}
button {
  display: block;
  width: 100%;
  height: 100%;
}
</style>
